.course-all {
  margin-top: 50px;
}

.course-all__block {
  @media (max-width: $mobile) {
    margin: 16px auto 0;
  }
}

.course-all__form {
  margin-bottom: 34px;
}

.course-all__button-box {
  display: none;
  justify-content: space-between;
  // max-width: 260px;

  @media (max-width: $mobile) {
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-start;
    gap: 7px;
    min-height: 40px;
  }
}

.course-all__item-btn--options {
  background-color: transparent;
}

.course-all__search {
  border: none;
  border-radius: 10px;
  padding: 14px 14px 14px 44px;
  margin-left: 35px;
  margin-bottom: 20px;
  background-color: $bg-light;
  color: $light-gray;
  font-size: 13px;
  background-image: url("../img/search-icon.svg");
  background-repeat: no-repeat;
  background-position: 19px 50%;
  //тень не актуальна
  // box-shadow: 0px 0px 16px rgba($black, 0.3);

  &::placeholder {
    color: $light-gray;
    opacity: 0.5;
    font-size: 13px;
  }

  @media (max-width: $mobile) {
    margin-left: 0;
    width: 100%;
  }
}

.course-all__title {
  margin-bottom: 50px;
  padding-right: 16px;
  padding-left: 16px;

  font-size: 32px;
  line-height: 1.2;

  @media (max-width: $mobile) {
    margin-top: 0;
    margin-bottom: 19px;
    padding-top: 18px;
    border-top: 1px solid $black;

    font-size: 25px;
    margin-bottom: 30px;
  }
}

.course-all__btn-options {
  margin-left: 6px;
  width: 37px;
  height: 40px;
  background-color: $bg-light;
  border-radius: 10px;
  border: none;
  display: flex;

  & img {
    width: 20px;
    height: 20px;
    margin: auto;
  }
}

.course-all__btn {
  height: 40px;
  border: none;
  box-sizing: border-box;
  padding: 12px 16px 10px;
  background-color: $bg-light;
  border-radius: 10px;

  font-size: 16px;
  font-weight: 400;
  text-align: center;
  text-decoration: none;
  color: $black;

  transition: background-color .3s;

  &:focus,
  &:hover {
    background-color: darken($main-color, 10);
  }

  &--active {
    background-color: $main-color;
    color: $white;
  }
}

.course-all__list {
  position: relative;
  display: grid;
  width: 100%;
  grid-template-columns: 1fr 1fr 1fr;
  row-gap: 40px;
  justify-content: space-between;
  padding: 0 35px;
  margin-top: 62px;

  @media (max-width: $mobile) {
    grid-template-columns: 1fr;
    padding: 0;
    justify-content: flex-start;
  }
}

.course-all__item {
  display: flex;
  flex-direction: column;
  max-width: 325px;
  min-height: 370px;
  padding: 25px;
  border-radius: 10px;
  box-shadow: 0 0 20px rgba(#000000, 0.1);

  @media (max-width: $mobile) {
    flex-basis: 272px;
    flex-shrink: 0;
    flex-wrap: nowrap;

    margin-right: 20px;
    padding-top: 30px;
    padding-right: 12px;
    padding-left: 12px;
    padding-bottom: 17px;
  }
}
.course-all__item--last{
  margin-right: 0;
}

.course-all__img-link {
  text-decoration: none;
}

.course-all__img {
  border-radius: 10px;

  @media (max-width: $mobile) {
    display: block;
    margin-bottom: 11px;
  }
}

.course-all__item-info {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.course-all__item-price {
  margin-top: 0;
  margin-bottom: 0;

  font-size: 25px;
  font-weight: bold;
  line-height: 1.3;
  color: $black;
  text-decoration: none;

  @media (max-width: $mobile) {
    font-size: 14px;
    line-height: 19px;
  }
}

.course-all__item-duration {
  margin-top: 0;
  margin-bottom: 0;

  font-size: 17px;
  font-weight: bold;
  line-height: 1.3;
  color: $main-color;

  @media (max-width: $mobile) {
    font-size: 9px;
    line-height: 14px;
    align-self: flex-end;
  }
}

.course-all__item-title {
  margin-top: 0;
  margin-bottom: 13px;

  font-size: 18px;
  font-weight: bold;
  line-height: 1.2;
  color: $black;
  transition: color .3s;

  &:hover,
  &:focus {
    color: $main-color;
  }
  @media (max-width: $mobile) {
    font-size: 12px;
    line-height: 16px;


  }
}

.course-all__item-btn {
  display: block;
  margin-top: auto;
  padding: 10px;
  font-size: 15px;
  font-weight: bold;
  line-height: 1.3;
  text-align: center;
  text-decoration: none;
  color: #fff;
  background-color: $main-color;
  border-radius: 10px;

  transition: background-color .3s;

  &:focus,
  &:hover {
    background-color: darken($main-color, 10);
  }

  @media (max-width: $mobile) {
    margin-top: 0;
    padding-top: 4px;
    padding-bottom: 5px;

    font-size: 10px;
    line-height: 14px;
  }
}

.course-all--full {
  position: relative;
  max-width: 1440px;
  margin: 70px auto 50px;

  @media (max-width: $mobile) {
    margin-top: 0;
    margin-bottom: 53px;
  }
}
