.search {
  flex-grow: 50;
  align-self: baseline;

  padding-top: 5px;

  @media (max-width: $mobile) {
    display: none;
  }
}

.search__form {
  position: relative;
  margin-left: 58px;
  margin-right: 102px;

  @media (max-width: $desktop) {
    margin-right: 70px;
    margin-left: 40px;
  }

  @media (max-width: $tablet) {
    margin-right: 30px;
    margin-left: 20px;
  }
}

.search__input {
  position: relative;
  width: 100%;
  height: 44px;
  padding: 0 160px 0 35px;
  border: 1px solid $main-color;
  border-radius: 10px;
  background-color: #F3F4F9;
  background-image: url('../../img/search-icon.svg');
  background-repeat: no-repeat;
  background-position: 11px center;
  background-size: 18px;

  &::placeholder {
    font-size: 13px;
    color: #4D4D4D;
  }

  @media (max-width: $tablet) {
    padding-right: 85px;
  }
}

.search__submit-btn {
  position: absolute;
  top: 0;
  right: 0;
  height: 100%;
  padding-top: 10px;
  padding-bottom: 10px;
  padding-right: 40px;
  padding-left: 39px;
  border: none;
  border-radius: 10px;

  font-size: 18px;
  font-weight: bold;
  line-height: 24px;

  color: $white;
  background-color: $main-color;
  transition: background-color .3s;

  cursor: pointer;

  &:focus,
  &:hover {
    background-color: darken($main-color, 10);
  }

  @media (max-width: $tablet) {
    padding-right: 20px;
    padding-left: 20px;
  }
}

